// React
import { useState, useRef, useCallback } from 'react';
// Library
import Editor from '@uiw/react-codemirror';
import { javascript } from '@codemirror/lang-javascript';
import { vscodeDark } from '@uiw/codemirror-theme-vscode';
// Components
import { Terminal } from './console';
import DropDownMenuBtn from '../../../../components/buttons/dropdown/dropdown';


const CodeEditor = (props:any) => {
  const codeEditorContent = useRef(`/*
  This is a JavaScript file, it exists only as a placeholder until you open a file from your server.
  To do so; please select the 'file' dropdown, press 'open' and then select a file to begin editing.
*/

console.log('hello world!');
`);
  const codeEditorConsole = useRef(``);
  const [ showConsole, setConsole ] = useState(false);
  const [ showDropDown, setDropDown ] = useState<any>(null);
  const [ consoleOutput, setConsoleOutput ] = useState("");
  const [ showFileBrowser, setFileBrowser ] = useState(false);
  const toggleDropDown = (setMenu:string) => showDropDown === setMenu ? setDropDown(null) : setDropDown(setMenu);
  const onChange = useCallback((value:string, viewUpdate:any) => {codeEditorContent.current = value;}, []);

  window.console.log = (output:any) => {
    if (typeof output == "object") {
      codeEditorConsole.current += `${JSON.stringify(output)}\n`;
    } else {
      codeEditorConsole.current += `${output}\n`;
    }
  }

  const DropDownMenuButton = (props:any) => {
    return <DropDownMenuBtn
      open={showDropDown === props.label}
      toggle={() => toggleDropDown(props.label)}
      label={props.label}
    />
  }

  const FileBrowserWindow = (props:any) => {
    return <>
      <div className="ep-window-fader" onClick={() => setFileBrowser(false)}/>
      <div className="ep-window">
        <div className="ep-window-title">File Browser</div>
        <i className="fa-solid fa-xmark ep-window-close" onClick={() => setFileBrowser(false)}/>
        <div className="ep-file-browser">
          <h1 style={{ color: 'white', margin: 'auto auto' }}>Feature Coming Soon...</h1>
        </div>
      </div>
    </>
  }

  const FileMenu = () => {
    return <div
      className="ep-dropdown"
      style={{ margin: '0 0 0 calc(-5vw + 8px)', minWidth: '150px' }}
      onClick={() => setDropDown('')}
    >
      <div className="ep-btn" onClick={() => {
        codeEditorContent.current = ``;
      }}>New File</div>

      <div className="ep-btn" onClick={() => {
        setFileBrowser(true);
      }}>Open File</div>

      <div className="ep-btn" onClick={() => alert("Feature Coming Soon...")}>Save</div>

      <div className="ep-btn" onClick={() => alert("Feature Coming Soon...")}>Save As...</div>

      <hr/>

      <div className="ep-btn" onClick={() => setConsole(!showConsole) }>
        {showConsole ? "Hide Console" : "Show Console"}
      </div>

      <div className="ep-btn">Exit</div>
    </div>
  }

  return <>
    <div className="ep-ui-toolbar">
      <DropDownMenuButton label="File"/>
      <button className="ep-btn-hover" onClick={() => {
        // Open console by default
        setConsole(true);
        // eslint-disable-next-line no-eval
        eval(codeEditorContent.current);
        // Update console output
        setConsoleOutput(codeEditorConsole.current);
      }}>
        <i className="fa-solid fa-circle-play"/>&nbsp;&nbsp;Run
      </button>
      <h3 className="ep-ui-header">Code Editor</h3>
    </div>
    { showDropDown === 'File' ? <FileMenu/> : <></> }
    { showFileBrowser ? <FileBrowserWindow/> : <></> }
    <div className={ showConsole ? "ep-code-editor-small" : "ep-code-editor" }>
      <Editor
        value={codeEditorContent.current}
        theme={vscodeDark}
        extensions={[javascript({ jsx: true })]}
        onChange={onChange}
      />
    </div>
    { showConsole ? <Terminal panel={props.panel} small={true} output={consoleOutput} input={false}/> : <></> }
  </>
}


export default CodeEditor;
