// React
import { useState, useEffect, useRef } from 'react';
import { useParams } from 'react-router-dom';
// Assets
import './e-panel.css'
import userIcon from '../../shared/assets/icons/userImage.svg';
import githubIcon from '../../shared/assets/icons/github.svg';
import settingsIcon from '../../shared/assets/icons/settings.svg';
// Library
import api from '../../shared/library/server/api';
import USER, { cookie } from '../../shared/library/local/user';
// Private Tabs
import UserProfile from './tabs/private/userProfile';
import UserGitHub from './tabs/private/userGitHub';
import UserDeviceSettings from './tabs/private/userDeviceSettings';
// Panel Tabs
import Admins from './tabs/panel/admins';
import Analytics from './tabs/panel/analytics';
import Posts from './tabs/panel/posts';
import Users from './tabs/panel/users';
// Server Tabs
import CodeEditor from './tabs/server/codeEditor';
import Console from './tabs/server/console';
import Logging from './tabs/server/logging';
import Settings from './tabs/server/settings';

// Global Variables
const _user = USER();
const _auth = `${_user.UUID} ${_user.SESH}`;
const desktopBreakpoint = 1250;
const cookie_AutoHideMenu = 'e-panel_AutoHideMenu';


const PanelView = () => {
  // State Management
  const [ currentView, setView ] = useState<string>('PanelAdmins');
  const [ permissions, setPermissions ] = useState<number>(-1);
  const [ panel, setPanel ] = useState<any>({
    id: useParams<any>().panelID
  });
  // Mobile, Tablet & Desktop Interface Manager
  const panelMenu = useRef<any>(null);
  const panelMenuBtn = useRef<any>(null);
  const panelMenuController = () => {
    // Acquire HTML elements
    if (panelMenu.current === null)
      panelMenu.current = document.querySelector('#Panel-Menu-Container') as HTMLElement;
    if (panelMenuBtn.current === null)
      panelMenuBtn.current = document.querySelector('#Panel-Menu-Button') as HTMLElement;
    // Set Default State
    if (panelMenu.current !== null) {
      if (!panelMenu.current.style.display) panelMenu.current.style.display = 'block';
      if (!panelMenuBtn.current.style.display) panelMenuBtn.current.style.display = 'none';
    }
  }
  const panelMenuSelect = (target:string) => {
    if (window.innerWidth < desktopBreakpoint && panelMenu.current !== null && panelMenuBtn.current !== null) {
      panelMenu.current.style.display = 'none';
      panelMenuBtn.current.style.display = 'block';
    }
    if (target !== currentView) setView(target);
  }

  // User Interface Resize Controller
  window.addEventListener('resize', () => {

    // Panel Menu
    if (panelMenu.current !== null && window.innerWidth >= desktopBreakpoint) {
      panelMenu.current.style.display = 'block';
      panelMenuBtn.current.style.display = 'none';
    } else if (panelMenu.current !== null && window.innerWidth < desktopBreakpoint) {
      if (panelMenu.current.style.display !== 'block') {panelMenuBtn.current.style.display = 'block';}
      else if (panelMenu.current.style.display === 'none') {panelMenuBtn.current.style.display = 'block';}
    }

  });

  // On Panel ID Selection or Change
  useEffect(() => {
    api(
      `verify/user/${panel.id}/${_user.UUID}`,
      (resp:any) => window.location.href = '/e-panel',
      (resp:any) => {
        setPermissions(resp.permissions);
      }
    )
    api(
      `view/${panel.id}`,
      (resp:any) => window.location.href = '/e-panel',
      (resp:any) => {
        resp.autoHideMenu = cookie(cookie_AutoHideMenu) !== null ? true : false
        resp.updatePanel = (_newData:any) => setPanel(_newData)
        setPanel(resp);
      }
    )
  }, [panel.id]);

  // On Mobile & Tablet Menu Selection
  useEffect(() => {panelMenuController()}, [panelMenu])
  useEffect(() => {
    if (window.innerWidth < desktopBreakpoint && panelMenu.current !== null && panelMenuBtn.current !== null) {
      panelMenu.current.style.display = 'none';
      panelMenuBtn.current.style.display = 'block';
    }
  }, [currentView])

  if (permissions < 0) return <></>
  return <div id="Panel-Body">
    <div id="Panel-Menu-Container" ref={panelMenu}>
      <div id="Panel-Menu" style={{ display: panel.autoHideMenu ? 'none' : 'flex' }}>

        <div id="Panel-Menu-Icons">
          <div className="Panel-Menu-Icon" onClick={() => panelMenuSelect("UserProfile")}>
            <img src={userIcon} alt="profile"/>
          </div>
          <div className="Panel-Menu-Icon" onClick={() => panelMenuSelect("UserGitHub")}>
            <img src={githubIcon} alt="github"/>
          </div>
          <div className="Panel-Menu-Icon" onClick={() => panelMenuSelect("UserDeviceSettings")}>
            <img src={settingsIcon} alt="user settings"/>
          </div>
        </div>

        <div id="Panel-Menu-Options">
          <div className="Panel-Menu-Header">Panel<div className="fa-solid fa-table-columns"/></div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Admins")}>
            <div className="fa-solid fa-shield-halved"/>
            <p>Admins</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Analytics")}>
            <div className="fa-solid fa-chart-simple"/>
            <p>Analytics</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Posts")}>
            <div className="fa-solid fa-file"/>
            <p>Posts</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Users")}>
            <div className="fa-solid fa-user"/>
            <p>Users</p>
          </div>

          <div className="Panel-Menu-Header">Server<div className="fa-solid fa-server"/></div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Console")}>
            <div className="fa-solid fa-terminal"/>
            <p>Console</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Logging")}>
            <div className="fa-solid fa-triangle-exclamation"/>
            <p>Logging</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("CodeEditor")}>
            <div className="fa-solid fa-code"/>
            <p>Code Editor</p>
          </div>
          <div className="Panel-Menu-Option" onClick={() => panelMenuSelect("Settings")}>
            <div className="fa-solid fa-screwdriver-wrench"/>
            <p>Settings</p>
          </div>
          <div id="Panel-Menu-Close" className="fa fa-times" onClick={() => {
            if (panelMenu.current !== null) {
              panelMenu.current.style.display = 'none';
              panelMenuBtn.current.style.display = 'block';
            }
          }}/>
        </div>

      </div>
    </div>

    <div
      id="Panel-Menu-Button"
      ref={panelMenuBtn}
      style={
        panelMenu.current === null ?
          {}
        :
          { display: panelMenu.current.style.display === 'none' ? 'block' : 'none' }
      }
      onClick={() => {
        if (panelMenu.current !== null) {
          panelMenu.current.style.display = 'block';
          panelMenuBtn.current.style.display = 'none';
        }
      }}
    ><i className="fa fa-bars"></i></div>

    <div id="Panel-Content">
      { currentView === 'UserProfile' ? <UserProfile panel={panel}/> : <></> }
      { currentView === 'UserGitHub' ? <UserGitHub panel={panel}/> : <></> }
      { currentView === 'UserDeviceSettings' ? <UserDeviceSettings panel={panel}/> : <></> }

      { currentView === 'Admins' ? <Admins panel={panel} permissions={permissions} /> : <></> }
      { currentView === 'Analytics' ? <Analytics panel={panel}/> : <></> }
      { currentView === 'Posts' ? <Posts panel={panel}/> : <></> }
      { currentView === 'Users' ? <Users panel={panel}/> : <></> }

      { currentView === 'CodeEditor' ? <CodeEditor panel={panel}/> : <></> }
      { currentView === 'Console' ? <Console panel={panel}/> : <></> }
      { currentView === 'Logging' ? <Logging panel={panel}/> : <></> }
      { currentView === 'Settings' ? <Settings panel={panel}/> : <></> }
    </div>
  </div>
}


export default PanelView
export { _user, _auth }
