// React
import { useState, useEffect } from 'react';
// Library
import { _user } from '../../e-panel-view';
import api, { xapi } from '../../../../shared/library/server/api';
// Components
import userIcon from '../../../../shared/assets/icons/userImage.svg';
import DropDownMenuBtn from '../../../../components/buttons/dropdown/dropdown';


const Admins = (props:any) => {
  const [ users, setUsers ] = useState<any>({});
  const [ showDropDown, setDropDown ] = useState<any>(null);
  const toggleDropDown = (setMenu:string) => showDropDown === setMenu ? setDropDown(null) : setDropDown(setMenu);

  const inviteNewUser = () => {
    xapi(`https://www.easter.company/api/user/invite`,
      (resp:any) => {
        if (resp.status === 'BAD') alert("User may already exist or has already been invited.");
        else if (resp.status === 'OK') alert("User has successfully been invited!");
      },
      JSON.stringify({
        email: (document.getElementById("invite-email-input") as HTMLInputElement).value,
        data: { ePanelInvite: props.panel.id },
        createdBy: _user.UUID
      })
    )
  }

  const DropDownMenuButton = (props:any) => {
    return <DropDownMenuBtn
      open={showDropDown === props.label}
      toggle={() => toggleDropDown(props.label)}
      label={props.label}
    />
  }

  const InviteAdminMenu = () => {
    return <form
      className="ep-dropdown"
      style={{
        margin: '0 0 0 calc(-5vw + 8px)'
      }}
      onSubmit={
        (event:any) => {event.preventDefault(); inviteNewUser();}
      }
    >
      <input id="invite-email-input" type="email" placeholder="user@example.net" className="ep-input"/>
      <button className="ep-btn-green" type="submit">
        <i className="fa fa-check">&nbsp;&nbsp;&nbsp;send</i>
      </button>
    </form>
  }

  const ShowInvitesMenu = () => {
    return <div
      className="ep-dropdown"
      style={{
        margin: '0 0 0 calc(-5vw + 174px)'
      }}
    >
      <h4>1 pending invite(s)</h4>
      <hr/>
      <h4>owen@easter.company</h4>
      <h5>sent: 30/10/2022 17:23</h5>
      <h5>expires: 30/01/2023</h5>
      <hr/>
    </div>
  }

  useEffect(() => {
    api(`view/${props.panel.id}/users`, (resp:any) => null, (resp:any) => setUsers(resp));
  }, [props.panel.id])

  return <>
    <div className="ep-ui-toolbar">
      <DropDownMenuButton label="Invite Admin"/>
      <DropDownMenuButton label="Show Invites"/>
      <h3 className="ep-ui-header">Admins</h3>
    </div>
    {
      showDropDown === 'Invite Admin' ? <InviteAdminMenu/> :
      showDropDown === 'Show Invites' ? <ShowInvitesMenu/> :
      <></>
    }
    <div id="panel-users">
      {Object.keys(users).map((user:any) => <div className="panel-users-user grow">
        <img src={userIcon} alt="profile"/>
        <p>{users[user].email}</p>
        <p style={{ marginLeft: 'auto', textAlign: 'right' }}>(<b>{users[user].permissions}</b>)</p>
      </div>)}
    </div>
  </>
}


export default Admins;
