// React
import { useState, useEffect } from 'react';
// Library
import { xapi } from '../../../../shared/library/server/api';
// Components
import Table from '../../../../shared/components/tables/table';


const Posts = (props:any) => {
  /*  STATE MANAGEMENT  */
  const [ _lastRequest, _setLastRequest ] = useState('');
  const [ tableType, setTable ] = useState('post');
  const [ head, setHead ] = useState([]);
  const [ body, setBody ] = useState([]);
  const [ utensils, setUtensils ] = useState([]);
  const [ ingredients, setIngredients ] = useState([]);

  let baseAPI = `https://${props.panel.panel}/${props.panel.api}/${tableType}`
  let listAPI = `${baseAPI}/list`;
  let createAPI = `${baseAPI}/create`;
  let deleteAPI = `${baseAPI}/delete`;
  let foreignAPI = `${baseAPI}/foreign`;

  /*  EFFECT CONTROLLER  */
  useEffect(() => {

    // Update Ingredients & Utensils Records
    const updateForeignKeys = () => xapi(
      foreignAPI,
      null, null,
      (resp:any) => {
        setUtensils(resp.data.utensils)
        setIngredients(resp.data.ingredients)
      }
    );

    // Update Current Table Data
    if (_lastRequest !== tableType) xapi(listAPI, null, null, (resp:any) => {
      setHead(resp.data.head);
      setBody(resp.data.body);
      _setLastRequest(tableType);
      if (tableType === 'recipe') updateForeignKeys();
    })

  }, [tableType, _lastRequest, listAPI, foreignAPI])

  /* UPDATE TABLE */
  const updateTable = () => xapi(
    listAPI,
    null, null,
    (resp:any) => {
      setHead(resp.data.head);
      setBody(resp.data.body);
    }
  );

  /* CREATE NEW RECORD API */
  const sendCreateRequest = (data:object) => {
    return xapi(
      createAPI,
      null, JSON.stringify(data),
      updateTable
    );
  }

  /* NEW POST VIEWS */
  const CreateNewRecord = () => {

    // Create New Recipe Record
    if (tableType === 'recipe') {
      return <>
        <div className="ep-create-record-view" style={{ display: 'flex' }}>
          <div style={{ maxWidth: '400px' }}>
            <p>Name:</p>
            <input type="text" id="recipe-name"/>
            <p>Categories:</p>
            <input type="text" id="recipe-categories"/>
            <p>Prep Time:</p>
            <input type="number" id="recipe-prep-time"/>
            <p>Cook Time:</p>
            <input type="number" id="recipe-cook-time"/>
          </div>
          <div style={{ width: "100%" }}>
            <p>Images:</p>
            <input type="text" id="recipe-images"/>
            <p>Description:</p><textarea id="recipe-description"/>
          </div>
        </div>
        <div>
          <p>Ingredients:</p>
          <select>
            {
              ingredients.map((ingredient:any) =>
                <option value={ingredient.uuid}>
                  {ingredient.name} ({ingredient.aliases})
                </option>
            )}
          </select>
          <p id="recipe-ingredients">1,2,3</p>
          <p>Utensils:</p>
          <input type="text" id="recipe-utensils" style={{
            backgroundColor: 'transparent', border: 'unset'
          }} />
          <select>
            {
              utensils.map((utensil:any) =>
                <option value={utensil.uuid}>
                  {utensil.name} ({utensil.aliases})
                </option>
            )}
          </select>
        </div>
      </>
    }

    // Create New Ingredients Record
    else if (tableType === 'recipe/ingredients') {
      return <div className="ep-create-record-view">
        <p>Name: </p><input type="text" id="ingredient-name"/>
        <p>Aliases: </p><input type="text" id="ingredient-aliases"/>
        <p>Icon: </p><input type="text" id="ingredient-icon"/>
        <p>Image: </p><input type="text" id="ingredient-image"/>
        <p>Description: </p><input type="text" id="ingredient-description"/>
        <p>Types: </p><input type="text" id="ingredient-types"/>
      </div>
    }

    // Create New Utensils Record
    else if (tableType === 'recipe/utensils') {
      return <div className="ep-create-record-view">
        <p>Name: </p><input type="text" id="utensil-name"/>
        <p>Aliases: </p><input type="text" id="utensil-aliases"/>
        <p>Icon: </p><input type="text" id="utensil-icon"/>
      </div>
    }

  }

  /*  PANEL POSTS VIEW  */
  return <>
    <div className="ep-ui-toolbar">
      <select
        style={{ maxWidth: '250px', margin: 'auto 24px auto 5vw' }}
        value={tableType}
        onChange={(event:any) => setTable(event.target.value)}>
        <option value="post">Generic</option>
        <option value="job">Job</option>
        <option value="recipe">Recipe</option>
        <option value="recipe/ingredients">Ingredients</option>
        <option value="recipe/utensils">Utensils</option>
      </select>
      <h3 className="ep-ui-header">Posts</h3>
    </div>
    <div id="Panel-Content-Container">
      <Table
        /* Config */
        name={tableType}
        previewSize={10}
        SVGFill="#282c34"
        /* Table Content */
        headers={head}
        data={body}
        /* API Paths */
        view={true}
        create={CreateNewRecord}
        delete={`${tableType}/delete`}
        /* Table Options */
        onDelete={(_uuid:any) => {
          xapi(
            deleteAPI,
            null, JSON.stringify({ uuid: _uuid }),
            (resp:any) => {
              updateTable();
            }
          );
        }}
        onCreate={() => {

          if (tableType === 'recipe') sendCreateRequest({
            name: (document.getElementById("recipe-name") as HTMLInputElement).value,
            categories: (document.getElementById("recipe-categories") as HTMLInputElement).value,
            images: (document.getElementById("recipe-images") as HTMLInputElement).value,
            description: (document.getElementById("recipe-description") as HTMLInputElement).value,
            prep_time: (document.getElementById("recipe-prep-time") as HTMLInputElement).value,
            cook_time: (document.getElementById("recipe-cook-time") as HTMLInputElement).value,
            ingredients: (document.getElementById("recipe-ingredients") as HTMLInputElement).value,
            utensils: (document.getElementById("recipe-utensils") as HTMLInputElement).value
          });

          else if (tableType === 'recipe/ingredients') sendCreateRequest({
            name: (document.getElementById("ingredient-name") as HTMLInputElement).value,
            aliases: (document.getElementById("ingredient-aliases") as HTMLInputElement).value,
            icon: (document.getElementById("ingredient-icon") as HTMLInputElement).value,
            image: (document.getElementById("ingredient-image") as HTMLInputElement).value,
            description: (document.getElementById("ingredient-description") as HTMLInputElement).value,
            types: (document.getElementById("ingredient-types") as HTMLInputElement).value
          });

          else if (tableType === 'recipe/utensils') sendCreateRequest({
            name: (document.getElementById("utensil-name") as HTMLInputElement).value,
            aliases: (document.getElementById("utensil-aliases") as HTMLInputElement).value,
            icon: (document.getElementById("utensil-icon") as HTMLInputElement).value
          });

        }}
      />
    </div>
  </>
}


export default Posts;
