// React
import { useState, useRef, useEffect } from 'react';
// Library
import './logging.css';
import { xapi } from '../../../../shared/library/server/api';
// Components
import { ReactTerminal, TerminalContextProvider } from "react-terminal";


const Logging = (props:any) => {
  const intervalSet = useRef(true);
  const [ consoleLogs, setLogs ] = useState("");

  useEffect(() => {
    const updateLogs = () => xapi(
      `https://${props.panel.name}/${props.panel.api}/o-core/logs?key=${props.panel.apiKey}`,
      (resp:any) => { if (resp.data.logs !== undefined) { intervalSet.current = true; setLogs(resp.data.logs); } }
    )
    updateLogs();
    const interval = setInterval(updateLogs, 5000);
    return () => clearInterval(interval);
  }, [props.panel])

  return <>
    <div className="ep-ui-toolbar">
      <h3 className="ep-ui-header" style={{ margin: '0 auto 0 5vw'}}>{props.panel.name}</h3>
      <h3 className="ep-ui-header">Logging</h3>
    </div>
    <TerminalContextProvider>
      <div className="ep-web-console logging-console">
        <ReactTerminal
          welcomeMessage={consoleLogs + '\n'}
          prompt={null}
          enableInput={false}
          showControlBar={false}
          showControlButtons={false}
          theme="material-dark"
        />
      </div>
    </TerminalContextProvider>
  </>
}


export default Logging;
