// React
import { useRef } from 'react';
// Library
import { _auth } from '../../e-panel-view';
import { xapi } from '../../../../shared/library/server/api';
import { ReactTerminal, TerminalContextProvider } from "react-terminal";
// Asset
import './console.css';


const Terminal = (props:any) => {
  // Console Memory References
  const consoleLine = useRef<number>(0);
  const consoleHistory = useRef<any>({});

  // Command Execution & Response
  const ExecuteCommand = (data:any, executionURL:any) => {
    const thisLine = consoleLine.current;
    consoleLine.current = consoleLine.current + 1;

    xapi(
      executionURL,
      (resp:any) => {
        consoleHistory.current[thisLine] = thisLine + ': ' + resp.data.output;
        for (let line = 0; line <= thisLine; line++) {
          try {
            const _el = document.querySelector(`#web-console-response-${line}`) as HTMLElement;
            _el.innerHTML = consoleHistory.current[line];
          } catch {}
        }
      },
      JSON.stringify({ command: data }),
      _auth,
      true
    );

    return <>
      <br/>
      <div
        id={`web-console-response-${thisLine}`}
        className="web-console-response"
        style={{ whiteSpace: 'break-spaces' }}
      />
    </>
  }

  // Generate Command Endpoint
  const URL = `https://${props.panel.name}/${props.panel.api}/o-core/external-command`;
  const CMD = (command:string) => ExecuteCommand(command, URL);

  // Command Keyword Object
  const clientCommands = {}

  // Terminal User Interface
  return <TerminalContextProvider>
    <div className={ props.small ? "ep-web-console-small" : "ep-web-console" }>
      <ReactTerminal
        enableInput={props.input === undefined ? true : props.input}
        welcomeMessage={props.output}
        errorMessage={(_input:any) => CMD(_input)}
        prompt={props.small ? "" : <b style={{ color: 'green' }}>{`>>`}</b>}
        showControlBar={false}
        showControlButtons={false}
        commands={clientCommands}
        theme="material-dark"
      />
    </div>
  </TerminalContextProvider>
}


const Console = (props:any) => {
  return <>
    <div className="ep-ui-toolbar">
      <h3 className="ep-ui-header" style={{ margin: '0 auto 0 5vw' }}>{props.panel.name}</h3>
      <h3 className="ep-ui-header">Web Console</h3>
    </div>
    <Terminal panel={props.panel}/>
  </>
}


export default Console;
export { Terminal };
