// React
import { useEffect, useState } from 'react';
// Library
import { xapi } from '../../../../shared/library/server/api';
// Components
import Table from '../../../../shared/components/tables/table';


const Users = (props:any) => {
  const [ head, setHead ] = useState([]);
  const [ body, setBody ] = useState([]);

  useEffect(() => {xapi(
    `https://${props.panel.name}/${props.panel.api}/user/view`,
    null, null,
    (resp:any) => { console.log(resp); setHead(resp.head); setBody(resp.body); }
  )}, [props.panel])

  return <>
    <div className="ep-ui-toolbar">
      <h3 className="ep-ui-header">Users</h3>
    </div>
    <div id="Panel-Content-Container" style={{
      flexDirection: 'column',
      textAlign: 'center',
      backgroundColor: '#f1ffff',
      padding: '16px 16px 16px 16px',
      borderRadius: '6px'
    }}>
      <Table
        /* Config */
        name="user"
        previewSize={10}
        SVGFill="#282c34"
        /* Table Content */
        headers={head}
        data={body}
        /* API Paths */
        view={'view'}
      />
    </div>
  </>
}


export default Users;
