import './dropdown.css';


const DropDownMenuBtn = (props:any) => {
  return <button
    className={props.open ? "ep-btn-selected" : "ep-btn"}
    style={{ width: '150px' }}
    onClick={() => props.toggle(props.label)}
  >
    { props.open ?
      <i className="fa fa-caret-down"></i>
      :
      <i className="fa fa-caret-right"></i>
    }
    &nbsp;&nbsp;{props.label}
  </button>
}


export default DropDownMenuBtn;
