// Default Home
import Home from './pages/home/home';
// Overlord Documentation
import Documentation from './pages/docs/docs';
// E-Panel Login & Registration;
import EPanelLogin from './pages/e-panel/e-panel-login';
import PanelView from './pages/e-panel/e-panel-view';
// Error Response Pages
import PageNotFoundError from './pages/error/404';
// Shared imports ------------------
import { Index, Route } from './shared/components/routes/routes';


// App routes
const Routes = () => {
  return <Index>

    { // [DEFAULT] Home Page
      Route({
        path: '',
        app: Home
      })
    }

    { // [Overlord] Documentation Page
      Route({
        path: 'overlord',
        app: Documentation
    })}

    { // [E-Panel] User Login Page
      Route({
        path: 'e-panel',
        app: EPanelLogin
    })}

    { // [E-Panel] View Panel Page
      Route({
        path: 'e-panel/:panelID',
        app: PanelView
    })}

    { // [ERROR 404] Page not found
      Route({
        path: '*',
        app: PageNotFoundError
    })}

  </Index>
}

export default Routes;
