import { xapi } from '../../../shared/library/server/api';


// Verifies whether or not a given URL is valid
export const isValidUrl = (URL:string) => {
  const pattern =
    /^https?:\/\/(?:www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)$/gm;
  return pattern.test(URL);
}


// Verifies whether or not a given URL responds with a 200 status code
export const verifyDomainStatus = async (URL:string, CALLBACK:any) => {
  try{
    if (isValidUrl(URL)) await fetch(URL).then(resp => CALLBACK(resp.ok));
    else return CALLBACK(false);
  } catch {
    return CALLBACK(false);
  }
}


// Verifies whether or not a given API URL returns an Overlord compatible response
export const verifyAPIStatus = async (URL:string, CALLBACK:any) => {
  try{
    xapi(URL, (resp:any) => {
      if (resp.status === "OK" && resp.data === "API Endpoint Root") return CALLBACK(true);
      else return CALLBACK(false);
    });
  } catch {
    return CALLBACK(false);
  }
}


// Verifies whether or not the API Key is valid
export const verifyKeyStatus = async (URL:string, CALLBACK:any) => {
  try{
    xapi(URL, (resp:any) => {
      if (resp.status !== undefined && resp.data === "Verified") return CALLBACK(true);
    });
  } catch {
    return CALLBACK(false);
  }
  return CALLBACK(false);
}


// Verifies the data displayed for the client list
export const verifyClientList = async (URL:string, CALLBACK:any) => {
  try{
    xapi(URL, (resp:any) => {
      if (resp.status !== undefined && resp.data !== undefined) {
        CALLBACK(resp.data);
      }
    });
  } catch {
    return CALLBACK([]);
  }
}


